body {
  font-family: Inter;
  margin: 0;
}

.header {
  display: flex;
  color: white;
  align-items: center;
  justify-content: center;
  background-color: #F55A5A;
  height: 100px;
  box-sizing: border-box;
  font-size: 20px;
}

.cards {
  display: flex;
  flex: 1 1 1;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.card {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 100px;
  width: 1000px;
}

.info {
  display: flex;
  flex-direction: column;
  flex: 1 2 1 4;
  gap: 10px;
  color: #2B283A;
}

.description {
  max-width: 80%;
}

.image {
  margin-right: 10px;
  max-width: 55px;
}

.Location {
  font-size: 20px;
}
.Location > a {
  font-size: 16px;
  color: #918E9B;
}

.place-name {
  font-size: 36px;
  margin-bottom: 5px;
  font-weight: bold;
}

.tour-date {
  font-size: 18px;
  font-weight: 600;
}

.description {
  font-size: 15px;
  line-height: 24px;
}